import { Box, Typography } from "@mui/material";
import React from "react";

/** 
 *   (  "")
 * @param {*} props 
 * @returns    title (+       )
 */
const CustomTitle = (props) => (
    <Box sx={{ mb: "32px", }}>
        <Typography variant="h3">{props.title}</Typography>
        {props.children}
    </Box>
)

export default CustomTitle;