import React from 'react';
import Carousel from 'react-material-ui-carousel';

const PhotoShower = ({ photoArr }) => (
    <Carousel>
        {
            photoArr.map((photo, i) =>
                <img src={"/images/photos/" + photo.srcP} alt={photo.srcP} key={i}
                    style={{ width: "100%", height: "auto", borderRadius: "32px" }}
                />
            )
        }
    </Carousel>
)

export default PhotoShower;