import CloseIcon from '@mui/icons-material/Close';
import MenuIcon from '@mui/icons-material/Menu';
import { Box, Button, IconButton, Link, Stack, SwipeableDrawer, useMediaQuery } from "@mui/material";
import React, { useState } from "react";
import theme from "../theme";

const linkStyle = { padding: "16px 24px", textDecoration: "none", borderRadius: "64px", color: "white", textTransform: "none" };
const menu = [
    {
        link: '#us',
        title: " ",
    },
    {
        link: '#interier',
        title: "",
    },
    {
        link: '#contacts',
        title: "",
    },
    {
        link: '#price',
        title: " ",
        sx: { backgroundColor: "#F1275B", color: "white", '&:hover': { color: "black" } },
    },
]

const TopMenu = () => {
    const isMatch = useMediaQuery(theme.breakpoints.down('md'))
    const [state, setState] = useState(false);
    return (
        <>
            {isMatch ?
                <>
                    <Stack direction={"row"} justifyContent={'space-between'} sx={{ width: "100%" }}>
                        <img src={"/logo.svg"} alt={"logo"} />
                        <IconButton sx={{ margin: "5% 10%", color: "white" }} onClick={(e) => setState(true)}><MenuIcon /></IconButton>
                    </Stack>
                    <SwipeableDrawer
                        anchor="right"
                        open={state}
                        onClose={(e) => setState(false)}
                        onOpen={(e) => setState(true)}
                        PaperProps={{
                            sx: { width: "100%", textAlign: "center", pt: "2%" },
                        }}
                    >
                        <Box sx={{ width: "100%", display: "flex", justifyContent: "flex-end", pr: "2%", pb: "5%" }}>
                            <IconButton onClick={(e) => setState(false)}>
                                <CloseIcon />
                            </IconButton>
                        </Box>
                        <Link href='#' onClick={(e) => setState(false)}><img src={"/logo.svg"} alt={"logo"} /></Link>
                        {menu.map((menuLink, key) => (
                            <Link href={menuLink.link} key={key}>
                                <Button sx={{ ...linkStyle, color: "black", m: "4px", ...menuLink.sx }} onClick={(e) => setState(false)}>{menuLink.title}</Button>
                            </Link>
                        ))
                        }
                    </SwipeableDrawer>
                </>
                :
                <Box sx={{ width: "96%", display: "flex", justifyContent: "space-between", }}>
                    <Link href='#'>
                        <img src={"/logo.svg"} alt={"logo"} />
                    </Link>
                    <Stack direction={"row"}>
                        {menu.map((menuLink, key) => (
                            <Link href={menuLink.link} key={key}>
                                <Button sx={{ ...linkStyle, ...menuLink.sx }}>{menuLink.title}</Button>
                            </Link>
                        ))
                        }
                    </Stack>
                </Box>
            }
        </>
    )
}

export default TopMenu;