import { Box, Link, Typography } from "@mui/material";
import React from "react";
import CustomTitle from "./CustomTitle";

const Footer = () => {
    return (
        <Box id="price" sx={{ backgroundColor: "#745291", width: "100%", display: "flex", justifyContent: "center", textAlign: "center", flexDirection: "column" }}>
            <Box sx={{ textAlign: "center", marginY: "60px" }}>
                <CustomTitle title="!" />
                <Link href="#" sx={{ color: "white", textDecorationColor: "white" }}></Link><br />
            </Box>
            <Typography variant="caption">pertefff 2025</Typography>
        </Box>
    )
}

export default Footer;