import { createTheme, } from '@mui/material';

let theme = createTheme({

    typography: {
        fontFamily: 'Montserrat, Arial',
        // fontSize: "1.3rem",
        h2: { fontWeight: "bold", },
        h3: { fontWeight: "bold", },
        h4: { fontWeight: "bold", },
        h5: { fontWeight: "bold", },
    },

    palette: {
        primary: {
            main: '#432603',
        },
        secondary: {
            main: '#FDFCF7',
        },
        third: {
            main: '#AA9F97',
        },
        pink: {
            main: '#F1275B',
            // main: '#D1B195',
        },
        buttons: {
            main: '#D1B195',
        },
        background: {
            default: '#FFAC90',
            // default: '#FDFCF7',
        }

    },

    breakpoints: {
        values: {
            xs: 0,   // < 
            sm: 650, // < 
            md: 960, // < 
            lg: 1280,
            xl: 1920,
        },
    },

    components: {
        MuiAccordionSummary: {
            styleOverrides: {
                root: {
                    padding: 0,
                    minHeight: "36px",
                    '&.Mui-expanded': {
                        minHeight: "36px",
                    },
                    '&:hover': {
                        opacity: 0.5,
                    }
                },
                content: {
                    margin: 0,
                    '&.Mui-expanded': {
                        margin: 0,
                    },
                },
            },
        },
        MuiAccordion: {
            styleOverrides: {
                root: {
                    backgroundColor: "inherit", 
                    boxShadow: "none", 
                    color: "white", 
                    '&::before': { display: "none" },
                    '&.Mui-expanded': {
                        margin: 0,
                    },
                }
            }
        },
        MuiAccordionDetails: {
            styleOverrides: {
                root: {
                    padding: "8px 0 16px",
                }
            }
        }
    },
});

// theme.spacing(2);

export default theme;