import { Box, Grid, Tooltip, Typography } from "@mui/material";
import React from "react";
import CustomTitle from "./CustomTitle";

const joyArray = [
    {
        img: "/images/Cafe.png",
        desc: "    : ,    ",
    },
    {
        img: "/images/SmilingFaceWithHeart.png",
        desc: "       ,  ,        ",
    },
    {
        img: "/images/DiceCubes.png",
        desc: "  ,     ",
    },
    {
        img: "/images/GameController.png",
        desc: "  , PS4, ,          ",
    },
    {
        img: "/images/Micro.png",
        desc: "   ,    ,  70000 ",
    },
]

const JoyCard = ({ img, desc }) => {
    return (
        // <Tooltip title="Add" arrow>
        <Grid item xs={12} sm={4}
            sx={{
                maxWidth: "340px",
                display: "flex",
                flexDirection: "column",
                alignItems: "center"
            }}>
            {/* <Box sx={{ maxWidth: "300px", display: "flex", flexDirection: "column", alignItems: "center" }}> */}
            <Box
                sx={{
                    backgroundColor: "white",
                    borderRadius: "50%",
                    padding: "8px",
                    width: "170px",
                    height: "170px",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    marginBottom: "16px",
                    '&:hover': {
                        width: "150px",
                        height: "150px",
                        marginTop: "10px",
                        marginBottom: "26px",
                    }
                }}>
                <img src={img} alt={img} width="70%" height="70%" />
            </Box>
            <Typography variant="text">{desc}</Typography>
            {/* </Box> */}
        </Grid>
        // </Tooltip >
    )
}

const WhatWeHave = () => {
    return (
        <Box sx={{ width: "80%", textAlign: "center", marginY: "60px" }}>
            <CustomTitle title="   ?" />
            <Grid container spacing={4} sx={{ justifyContent: "center" }}>
                {joyArray.map((item, key) => (
                    <JoyCard img={item.img} desc={item.desc} key={key} />
                ))}
            </Grid>
        </Box>
    );
}

export default WhatWeHave;