import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Box, Icon, Stack } from "@mui/material";
import React from "react";

//      children,      

/**
 *  (text)   (src) 
 * @param {*} props 
 * @returns 
 * @argument src, text, fontWeight=false, isEndIcon=false
 */
const CustomListItem = (props) => {
    const { src, sx, isEndIcon = false } = props;
    return (
        <Stack direction={"row"}
            sx={{
                alignItems: "center",
                marginBottom: "8px",
                width: "100%",
                ...sx,
            }}>
            {src &&
                <img src={src} alt="ops"
                    style={{
                        width: "1.5em",
                        marginRight: "2%"
                    }} />
            }
            <Box>
                {props.children}
            </Box>
            <Icon
                sx={{
                    display: isEndIcon ? "inline-block" : "none",
                    justifyContent: "right"
                }}
            >
                <ExpandMoreIcon style={{ color: "white" }} />
            </Icon>
        </Stack>
    )
}
export default CustomListItem;